*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  
  height: 100vh;
  width: 100%;
  background: #191720;
  color: white;
}
.heading{
  font-size: 50px;
  font-weight: 400;
}
.App{
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items:center;
  flex-direction: column;
}
.container{
  display: flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
  margin-top: 30px;
  border:dashed #dadada 3px;
  width:60vw;
  border-radius: 13px;

}
.list{
  height: 60px;
  width: 70%;
  border-radius: 13px;
  margin: 10px;
  background: #2f2f2f;
  overflow: hidden;
  display: flex;
  padding-left: 30px;
  padding-right: 30px;
  justify-content:space-between;
  align-items:center;
}
.list input{
  height: 60px;
  width: 100%;
  font-size: 35px;
  background: #2f2f2f;
  border: none;
  outline: none;
  color: white;
  padding-left: 5px;
  border-radius: 13px;
}
.inputs{
  margin-top: 30px;
  display: flex;
  justify-content:space-evenly;
  align-items:center;
  width: 50%;
  
}
.inputs input{
  height: 50px;
  width: 500px;
  border-radius: 13px;
  font-size: 30px;
  border: none;
  outline: none;
  padding-left: 10px;
}
.btn{
  height:50px ;
  border-radius: 7px;
  width: 120px;
  background: rgb(30, 36, 38);
  font-size: 20px;
  color: white;
}
.icon{
  cursor: pointer;
  font-size: 30px;
}

.search{
  width: 100%;
  background: rgb(48, 48, 48) ;
  height: 60px;
  display: flex;
  padding-right: 100px; 
  justify-content: end;
  align-items: center;
  
}

.search input{
  background: #303030;
  height: 90%;
  width: 300px; 
  font-size: 25px;
  border-radius: 7px; 
  padding-left: 20px; 
  border: solid;
  outline: none;
  color: #dadada;
}